import React from 'react';
import Header from '../header';
import Menu from '../menu';
import './Plans.scss';

const Plans = () => {
  const hours = [
    ['08:55', '09:40', 10],
    ['09:50', '10:35', 15],
    ['10:50', '11:35', 10],
    ['11:45', '12:30', 30],
    ['13:00', '13:45', 15],
    ['14:00', '14:45', 10],
    ['14:55', '15:40', 5],
    ['15:50', '16:00', 0],
  ];
  const subjects = [
    '---',
    'Polski',
    'Matematyka',
    'Programowanie',
    'Angielski',
  ];

  const classes = [
    'Klasa 1 gr. 1',
    'Klasa 1 gr. 2',
    'Klasa 2',
  ]

  return (
    <div className="plans-container">
      <Header></Header>
      <Menu></Menu>
      <div className="content">
        <h1>Edytuj plan zajęć</h1>
        <select className="classes">
          {classes.map((className, index) => {
            return <option key={index}>{className}</option>
          })}
        </select>
        <div className="plan">
          <div className="col">
            <p className="sub-name">Poniedziałek</p>
            {hours.map((hour, index) => {
              return <div className="sub" key={index}>
                <span>{index + 1}. {hour[0]}-{hour[1]} </span>
                <select>
                  {subjects.map((subject, index) => {
                    return <option key={index}>{subject}</option>
                  })}
                </select>
              </div>
            })}
          </div>
          <div className="col">
            <p className="sub-name">Wtorek</p>
            {hours.map((hour, index) => {
              return <div className="sub" key={index}>
                <span>{index + 1}. {hour[0]}-{hour[1]} </span>
                <select>
                  {subjects.map((subject, index) => {
                    return <option key={index}>{subject}</option>
                  })}
                </select>
              </div>
            })}
          </div>
          <div className="col">
            <p className="sub-name">Środa</p>
            {hours.map((hour, index) => {
              return <div className="sub" key={index}>
                <span>{index + 1}. {hour[0]}-{hour[1]} </span>
                <select>
                  {subjects.map((subject, index) => {
                    return <option key={index}>{subject}</option>
                  })}
                </select>
              </div>
            })}
          </div>
          <div className="col">
            <p className="sub-name">Czwartek</p>
            {hours.map((hour, index) => {
              return <div className="sub" key={index}>
                <span>{index + 1}. {hour[0]}-{hour[1]} </span>
                <select>
                  {subjects.map((subject, index) => {
                    return <option key={index}>{subject}</option>
                  })}
                </select>
              </div>
            })}
          </div>
          <div className="col">
            <p className="sub-name">Piątek</p>
            {hours.map((hour, index) => {
              return <div className="sub" key={index}>
                <span>{index + 1}. {hour[0]}-{hour[1]} </span>
                <select>
                  {subjects.map((subject, index) => {
                    return <option key={index}>{subject}</option>
                  })}
                </select>
              </div>
            })}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Plans;
