import React from 'react';
import { useCookies } from 'react-cookie';
import { useNavigate } from 'react-router-dom';
import { authService } from '../../services/auth.service';
import './Login.scss';

const Login = () => {
  // eslint-disable-next-line
  const [cookies, setCookie, removeCookie] = useCookies(['app-token']);
  const navigate = useNavigate();
  const loginRef = React.createRef();
  const passRef = React.createRef();

  const loginFn = (e) => {
    e.preventDefault();

    const login = loginRef.current.value;
    const password = passRef.current.value;

    const data = { login, password };

    authService.login(data)
    .then((res) => {
      if (res.data.success) {
        setCookie('app-token', res.data.token, { path: '/', maxAge: 3600 });
        navigate('/home');
      }
    });
  }

  return (
    <div className="login-container">
        <form className="form">
          <input ref={loginRef} type="text" placeholder="name"></input>
          <input ref={passRef} type="password" placeholder="password"></input>
          <button onClick={e => loginFn(e)}>Login</button>
        </form>
    </div>
  );
};

export default Login;
