import React from 'react';
import './Home.scss';
import Header from '../header';
import Menu from '../menu';
import { authService } from '../../services/auth.service';
import { useCookies } from 'react-cookie';
import { useNavigate } from 'react-router-dom';

const Home = () => {
  // eslint-disable-next-line
  const [cookies, setCookie, removeCookie] = useCookies(['app-token']);
  const token = { token: cookies['app-token'] };
  const navigate = useNavigate();

  authService.checkSession(token)
  .then((res) => {
    if (!res.data.success) {
      removeCookie('app-token');
      navigate('/');
    }
  });

  return (
    <div className="home-container">
        <Header></Header>
        <Menu></Menu>
        <div className="content">
          <h1>Zaprojektowano przez: Prowadzący</h1>
          <p>Aplikacja do tworzenia planu zajęć dla klas w Technischools.</p>
        </div>
    </div>
  );
};

export default Home;
