import React from 'react';
import { Link } from 'react-router-dom';
import './Menu.scss';

const Menu = () => {
  return (
    <div className="menu-container">
      <ul>
        <li>
          <Link to="/home">Home</Link>
        </li>
        <li>
          <Link to="/home/classes">Klasy</Link>
        </li>
        <li>
          <Link to="/home/plans">Plany</Link>
        </li>
        <li>
          <Link to="/home/subjects">Przedmioty</Link>
        </li>
      </ul>
    </div>
  );
};

export default Menu;
